import { MockSDataCulture } from '../../test/mocks/mockSDataCultureClass';

export class CultureService {
  constructor(cacheService = null) {
    this.cache = cacheService;
    this.activeTag = 'ActiveCodes';
    this.allTag = 'AllCodes';
    this.nameSpace = 'CultureCodes';
    this.neutralTag = 'NeutralCodes';
    this.regionTag = 'RegionCodes';
  }
  getAllCodes(query, params) {
    this.getFromCache(this.allTag, query, params);
  }
  getNeutralCodes(query, params) {
    this.getFromCache(this.neutralTag, query, params);
  }
  getRegionCodes(query, params) {
    this.getFromCache(this.regionTag, query, params);
  }
  getActiveCodes(query, params) { // expects the path, ~/App_Data/LanguageList.xml, to exist on the Request's path. See Sage.SalesLogic.Web.EnabledLanguageList.cs
    this.getFromCache(this.activeTag, query, params);
  }
  getFromCache(tag, query, params) {
    const data = this.cache.getItem(this.nameSpace, tag) || null;
    if (!(params || {}).onSuccess) {
      throw new Error('A callback function of onSuccess is expected by the method. Without it no data can be accessed.');
    }
    if (!data) {
      this.createQueryForCodes(query, tag, params);
    } else {
      this.onSuccess(data, tag, this, params.onSuccess);
    }
  }
  createQueryForCodes(query, tag, params) {
    const me = this; // incase scope is redefined by param
    if (query === null || typeof query === 'undefined' || (params || {}).Test) {
      console.warn('no request is being made, pulling from mock data'); //eslint-disable-line
      me.onSuccess(new MockSDataCulture().makeRequest(), tag, me, (params || {}).onSuccess);
    } else {
      if (!query.read) {
        throw new Error('The method needs to retrieve data, but the query object is missing the expected \'read\' function.');
      }
      query.read({
        async: true, // <-setting to false causes warning in SlxClient so force it to be true.
        success: (data) => {
          me.onSuccess(data, tag, me, (params || {}).onSuccess);
        },
        failure: (params || {}).onFail || function onFail(o) { console.warn('%o', o); }, //eslint-disable-line
        scope: (params || {}).scope || me,
      });
    }
  }
  onSuccess(data, tag, scope, callback) { /* marked as a method that should be static, but is a helper method only used in createQueryForCodes method. *///eslint-disable-line
    const resource = data.$resources ? data.$resources[0] : data;
    let cultureList = [];
    if (resource.response) {
      const lists = resource.response;
      if (lists.allLanguages) {
        const getResult = this.setAndGetFromCache(scope.cache, scope.nameSpace, scope.allTag, lists.allLanguages, scope.allTag === tag);
        cultureList = getResult === null ? cultureList : getResult;
      }
      if (lists.neutralLanguages) {
        const getResult = this.setAndGetFromCache(scope.cache, scope.nameSpace, scope.neutralTag, lists.neutralLanguages, scope.neutralTag === tag);
        cultureList = getResult === null ? cultureList : getResult;
      }
      if (lists.regionLanguage) {
        const getResult = this.setAndGetFromCache(scope.cache, scope.nameSpace, scope.regionTag, lists.regionLanguage, scope.regionTag === tag);
        cultureList = getResult === null ? cultureList : getResult;
      }
      if (lists.activeLanguages) {
        const getResult = this.setAndGetFromCache(scope.cache, scope.nameSpace, scope.activeTag, lists.activeLanguages, scope.activeTag === tag);
        cultureList = getResult === null ? cultureList : getResult;
      }
    } else {
      cultureList = resource;
    }
    if (callback) {
      callback(typeof (cultureList) === 'string' ? JSON.parse(cultureList) : cultureList);
    }
  }
  setAndGetFromCache(cache, nameSpace, tagUsed, data, skipGet) {  /* marked as a method that should be static, but is a helper method only used in onsucess method. *///eslint-disable-line
    cache.setItem(nameSpace, tagUsed, null, data);
    return skipGet ? cache.getItem(nameSpace, tagUsed) || [] : null;
  }
}



